
html {
  background-color: #f9f9f9;
}
#app {
  font-family: "Inter var experimental", "Inter var", Inter, -apple-system,
    BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #34495e;
  margin-top: 20px;
}
.logo {
  width: 140px;
}

h1[data-v-46735cbd],
h2[data-v-46735cbd] {
  font-weight: bold;
}
h1[data-v-46735cbd] {
  margin-top: 0px;
}
ul[data-v-46735cbd] {
  list-style-position: inside;
}
a[data-v-46735cbd] {
  color: #42b983;
}

table {
  margin: 0 auto;
  margin-top: 20px;
  border: 2px solid #42b983;
  border-radius: 6px;
  background-color: #fff;
}
th {
  background-color: #42b983;
  color: rgba(255, 255, 255, 0.66);
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
td {
  background-color: #dddcdc;
}
th,
td {
  min-width: 120px;
  padding: 8px 16px;
}
th.active {
  color: #fff;
}
th.active .arrow {
  opacity: 1;
}
.arrow {
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 0;
  margin-left: 5px;
  opacity: 0.66;
}
.arrow.asc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #fff;
}
.arrow.dsc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid #fff;
}

h2 {
  margin-top: 40px;
}
a {
  text-decoration: none;
  color: #42b883;
}
li {
  line-height: 1.4em;
  margin-bottom: 18px;
  list-style: none;
}
.branch-item {
  display: inline-block;
  margin-right: 16px;
}
.author,
.buttons,
.date {
  font-weight: bold;
}
.commit-msg {
  font-style: italic;
}
.counter {
  font-size: 1.2em;
}
